







:root {
  --footer-bg-color: #9bb537;
  --footer-text-color: #dcee92;
  --footer-link-color: #292424;
  --footer-link-bg-color: #cfcece;
  --btn-to-top-bg-color: #f68787;
  --btn-to-top-fill-color: #5ba016;
  --focus-visible-box-shadow-color: rgba(0, 0, 0, .5);
}

.site-footer {
  background-color: var(--footer-bg-color);
  color: var(--footer-text-color);
  padding: 20px 0;
}

.footer-content {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer-section {
  text-align: center;
  padding: 10px;
}

.f-list {
  justify-content: space-between;
  gap: 20px;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.footer-media-item {
  margin: 0;
}

.footer-media-link {
  color: var(--footer-link-color);
  width: 40px;
  height: 40px;
  background-color: var(--footer-link-bg-color);
  color: var(--footer-text-color);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  transition: color .3s, background-color .3s;
  display: flex;
}

.footer-media-icon {
  width: 24px;
  height: 24px;
  fill: currentColor;
}

.footer-media-link:hover, .footer-media-link:focus {
  background-color: var(--footer-text-color);
  outline: none;
}

.footer-media-link:focus-visible {
  box-shadow: 0 0 0 3px var(--focus-visible-box-shadow-color);
}

.footer-media-title {
  color: var(--footer-text-color);
  text-align: center;
  margin-bottom: 16px;
  font-size: 18px;
}

#btn-to-top {
  width: 50px;
  height: 50px;
  background-color: var(--btn-to-top-bg-color);
  fill: var(--btn-to-top-fill-color);
  cursor: pointer;
  opacity: .6;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  transition: opacity .3s;
  display: none;
  position: fixed;
  bottom: 30px;
  right: 30px;
}

#btn-to-top.is-visible {
  display: flex;
}

#btn-to-top:hover {
  opacity: 1;
}

@media screen and (min-width: 768px) {
  .footer-content {
    flex-direction: row;
    justify-content: space-between;
  }

  .footer-section {
    text-align: left;
  }

  .f-list {
    gap: 30px;
  }
}

@media screen and (min-width: 1280px) {
  .footer-content {
    padding: 0 40px;
  }

  .footer-section {
    padding: 15px;
  }
}













body {
  color: #050505;
  background-color: #f8f8f8;
  font-family: Inter;
  font-weight: 500;
}

.container {
  width: 100%;
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

html {
  scroll-behavior: smooth;
}

